.card:hover{
    box-shadow: 5px 10px 20px 1px rgba(238, 6, 18, 0.273)!important;
    transition: all 0.7s linear;
}

.card-body{
    padding: 2rem 0 !important;
}

.card-text{
    padding: 0.3rem 1rem;
    font-size: 1rem;
}


.card-img-top{
    transition: all 0.2s ease-in-out;
}

.overflow{
    overflow:hidden;
}


.card-img-top:hover{
    transform: scale(1.8);
}